body {
    background: #fafafa;
}

#about {
    padding:24px;
    margin-top: 20vh;
    width: 60%;
    background: white;
    color: #5c5c5c;
    margin: auto;
    text-align: center;
}

#about #head {
    font-weight: lighter;
    color: #5c5c5c;
    font-size: 28px;
    letter-spacing: 4px;
}

#about p {
    text-align: start;
    margin: 0px 24px 0px 24px;
    font-weight: normal;
    word-spacing: 5px;
    font-size: 16px;
    color: #5c5c5c;
}

#about img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
}

@media(max-width:500px){
    #about {
        width: 90%;
    }
}

#datas {
    display: flex;
    flex-wrap: wrap;
    text-align: start;
    justify-content: center;
    margin:auto;
    width: 90%;
}

#about .info {
    max-width: 89%;
    padding: 24px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
}  

#about .info h3 {
    margin: 0px;
    padding: 0px;
    font-size: 22px;
    font-weight: bold;
}

#about .info h4 {
    margin: 0px;
    padding: 0px;
    font-size: 16px;
}

#about #members {
    margin-top: 32px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

#members img {
    width: 100px;
    height: 100px;
}

.member {
    width: 33%;
}

.member:nth-child(4),.member:nth-child(5),.member:nth-child(6){
    margin-top: 24px;
}

@media(max-width:700px){
    .member {
        margin-top: 24px;
        width: 50%;
    }
}